<script>
import { Doughnut } from "vue-chartjs";
import store from "../store";
import DoughnutChartVue from './DoughnutChart.vue';

export default {
  extends: DoughnutChartVue ,
  data() {
    return {
      chartData: {
        labels: [
          "Payment",
          "Bonus",
          "PartTime",
          "Invest",
          "Allowance",
          "Others",
          "Undefined"
        ],
        datasets: [
          {
            borderWidth: 1,
            borderColor: [
              "rgba(255,99,132,1)",
              "rgba(233, 80, 10, 1)",
              "rgba(255, 206, 86, 1)",
              "rgba(10, 233, 122, 1)",
              "rgba(54, 162, 235, 1)",
              "rgba(153, 102, 255, 1)",
              "rgba(100, 100, 100, 1)"
            ],
            backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(233, 80, 10, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(10, 233, 122, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(153, 102, 255, 0.2)",
              "rgba(100, 100, 100, 0.2)"
            ],
            data: [
              parseInt(store.state.revenueEarn.payment),
              parseInt(store.state.revenueEarn.bonus),
              parseInt(store.state.revenueEarn.parttime),
              parseInt(store.state.revenueEarn.invest),
              parseInt(store.state.revenueEarn.allowance),
              parseInt(store.state.revenueEarn.rOthers),
              parseInt(store.state.revenueEarn.rUndefined),
            ]
          }
        ]
      },
    //   options: {
    //     legend: {
    //       display: true
    //     },
    //     responsive: true,
    //     maintainAspectRatio: false
    //   }
    };
  },
  mounted() {
    this.renderChart(this.chartData, this.options);
  }
};
</script>
